function menu_click() {
  var element = document.getElementById("ham_list");
  element.classList.toggle("Ninja-header-hamburger-list-show");
}
function menu_select() {
  var element = document.getElementById("ham_list");
  element.classList.toggle("Ninja-header-hamburger-list-show");
}

